/*--------------------------------------------------------------
# Normalize
--------------------------------------------------------------*/
@import 'common/variables';
/*--------------------------------------------------------------
# Custom Css
--------------------------------------------------------------*/
.container {
    max-width: 1090px;
}
.wtb-breadcrumb {
    .breadcrumb-banner {
        padding-top: 300px;
    }
    .breadcrumb-content {
        top: 11.3688525%;
        .title-bar-wrap h1 {
            text-align: left;
        }
    }
}
.contact-content {
    margin-top: -60px;
}
.address-list {
    padding: 30px;
    background:  url('../img/bg-e-ct.svg') center bottom no-repeat, #F2FBFF;
    border-radius: 8px;
    border: 1px solid rgba($color: #0086D1, $alpha: 0.2);
    box-shadow: 10px 10px 10px rgba(0, 24, 86, 0.05);
    .address {
        span {
            font-weight: 600;
        }
        p {
            margin-bottom: 0;
        }
    }
    .address-item {
        margin-bottom: 20px;
        &:first-child {
            border-bottom: 1px solid rgba($color: #0086D1, $alpha: 0.2);
            padding-bottom: 20px;
            margin-bottom: 20px;
        }
    }
}
.contact-wrap__content {
    position: relative;
    margin-bottom: 45px;
}
.form-block {
    z-index: 2;
    background: linear-gradient(180deg, #266ECC 0%, #0C5BBD 100%);
    border: 1px solid rgba(255, 255, 255, 0.5);
    border-radius: 13px;
    padding: 30px;
    color: $white;
    width: 100%;
    margin: 0 auto;
    .form-excerpt {
        font-weight: normal;
        span {
            display: block;
            font-weight: 600;
        }
    }
    .form-contact {
        .form-group {
            margin-bottom: 8px;
        }
        .form-control {
            height: 40px;
            background: rgba($color: #FFFFFF, $alpha: 0.2);
            border: 1px solid rgba($color: #FFFFFF, $alpha: 0.3);
            border-radius: 10px;
            padding: 10px 16px;
            font-weight: 500;
            color: $white;
            &::placeholder {
                color: rgba($color: #FFF, $alpha: 0.65);
            }
        }
        textarea.form-control {
            height: 110px;
        }
        .wpcf7-not-valid-tip {
            color: $white;
            font-size: 12px;
            font-weight: 500;
        }
        .form-submit {
            text-align: center;
            position: relative;
            margin-top: 13.5px;
            .btn-submit {
                background: $white;
                border-radius: 32.5px;
                width: 100%;
                height: 40px;
                color: $text-color;
                font-weight: 500;
            }
            .custom-submit {
                display: inline-block;
                border: 4px solid rgba(255, 255, 255, 0.2);
                border-radius: 32.5px;
                position: relative;
                width: 60%;
                .wpcf7-spinner {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%,-50%);
                    margin: 0;
                }
            }
        }
    }
    .wpcf7-response-output {
        color: $text-color;
        padding: 5px !important;
        margin: 10px 0 !important;
        font-size: 14px;
    }
}
/*--------------------------------------------------------------
# Responsive
--------------------------------------------------------------*/
@media only screen and (min-width: 576px) {
    .address-list {
        padding: 40px;
    }
    .contact-content {
        margin-top: -80px;
    }
}
@media only screen and (min-width: 850px) {
    .form-block {
        width: 60%;
        margin: 0 auto;
    }
}
@media only screen and (min-width: 992px) {
    .wtb-breadcrumb {
        .breadcrumb-banner {
            padding-top: 360px;
        }
    }
    .contact-content {
        margin-top: -90px;
    }
    .form-block {
        position: absolute;
        width: 49.0566038%;
        right: 0;
        top: 25px;
        padding: 20px 35px;
    }
    .address-list {
        padding: 50px 37% 115px 42px;
    }
}
@media only screen and (min-width: 1200px) {
    .wtb-breadcrumb {
        .breadcrumb-banner {
            padding-top: 420px;
        }
    }
    .contact-content {
        margin-top: -120px;
    }
}
@media only screen and (min-width: 1400px) {

    .wtb-breadcrumb {
        .breadcrumb-banner {
            padding-top: 488px;
        }
    }
    .contact-content {
        margin-top: -157px;
    }
}
@media only screen and (min-width: 1600px) {
    
}